.contacts {
  background-color: #ffffff;
  color: #000000;
}

.contacts__title {
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  color: #283645;
  text-align: center;
}

.contacts__subtitle {
  font-weight: 700;
  font-size: 18px;
  line-height: 25px;
  color: #283645;
}

.contacts__text {
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  color: #d22856;
  text-decoration: none;
}

.contacts__map-tablet,
.contacts__map-desktop {
  display: none;
}


@media (min-width: @tablet-width) {
  .contacts__map-mobile,
  .contacts__map-desktop {
    display: none;
  }

  .contacts__map-tablet {
    display: block;
  }
}

@media (min-width: @desktop-width) {
  .contacts__map-mobile,
  .contacts__map-tablet {
    display: none;
  }

  .contacts__map-desktop {
    display: block;
  }
}

.contacts__wrapper {
  @media (min-width: @tablet-width) {
    display: flex;
  }
}
